.main-bar{
    float: left;
    margin: 25px 10px;
    padding-right: 100px;
}
.main-bar-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 120%;
}
.main-bar-header h1{
    font-weight: 400;
}
.main-bar-header .ask-btn{
    padding: 10px 15px;
    border-radius: 4px;
    background-color: #009dff;
    color: white;
    text-decoration: none;
    transition: 0.3s;
}
.main-bar-header .ask-btn:hover{
    background-color: #0086d8;
}
.ask-btn{
    padding-left: 30px;
}
.display-question-container{
    min-height: 100px;
    width: 120%;
    display: flex;
    align-items: center;
    background-color: #fdf7e2;
    border-bottom:solid 1px #edeff0;
}
.display-question-container .display-votes-ans{
        padding: 20px;
}
.display-question-container .display-votes-ans p{
    margin: 0%;
    text-align: center;
}
.display-question-details{
    flex-grow: 1;
    padding: 0px 20px;
}
.question-title-link{
    text-decoration: none;
    color: #037ecb;
    transition: 0.3s;
}
.question-title-link:hover{
    color: #009dff;
}
.display-question-details p{
    padding: 4px;
    margin: 0%;
}
.display-tags-time .display-tags{
    display: flex;
    flex-wrap: wrap;
}
.display-tags-time{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.display-tags-time .display-tags{
    margin: 2px;
    padding: 4px;
    font-size: 13px;
    background-color: #edeff0;
    color: #39739d;
}
.display-tags-time .display-time{
    font-size: 13px;
}
.ask-form-container label input,
.ask-form-container label textarea{
    padding: 10px;
    border:solid 1px #0000003e;
    font-family :'Roboto',sans-serif;
    width:100%;
    resize: vertical;
}
.review-btn{
    margin: 50px 0px;
    padding: 10px;
    background-color: #009dff;
    border: solid 1px #009dff;
    color: white;
    border-radius:4px ;
    cursor: pointer;
    transition: 0.3s;
}
.review-btn:hover{
    background-color: #0086d8;
}